<template>
    <v-card>
        <v-card-title primary-title>
            <div>
                <h2 class="mb-0"></h2>
                <p>         .       .</p>
                <v-checkbox label=" "
                            v-model="product.montaj.isUsed"
                            :input-value="product.montaj.isUsed"
                            :false-value="false"
                            :true-value="true"
                            color="success"/>
            </div>
        </v-card-title>

        <v-card-text>

            <div v-if="product.montaj.isUsed">

                <h2>        </h2>

                <v-text-field label="  *"
                              v-model="product.montaj.step"
                              hint="      "/>

                <v-text-field label="  *"
                        v-model="product.montaj.name"
                        hint="      "/>

                <v-layout v-for="(item, index) in product.montaj.items" :key="index">
                    <v-flex md4>
                        <v-text-field
                                label="  *"
                                v-model="product.montaj.items[index].name"
                                hint=" "/>
                    </v-flex>

                    <v-flex md4 class="mx-3">
                        <v-text-field
                                label="   ()"
                                v-model="product.montaj.items[index].price"
                                hint="  "/>
                    </v-flex>

                    <v-flex md4>
                        <v-text-field label=" "
                                      v-model="product.montaj.items[index].thumb"
                                      :hint="product.base.nameDB ? `${product.base.nameDB}/${sectionName}/thumb/-.jpg` : '!!   ( 1)'"/>
                    </v-flex>

                    <v-btn  icon color="primary" flat @click="openDialog(index)" :disabled="index === 0">x</v-btn>

                </v-layout>

                <v-layout>
                    <v-spacer/>
                    <v-btn color="primary" flat @click="addItem">
                          
                    </v-btn>
                </v-layout>

                <delete-entry :is-open="dialog" @deleteItem="deleteItem" @dialogToggle="val => dialog = val" dialog-header=" " dialog-body="    ?"/>

                <v-textarea name="input-7-1"
                            label="  ()"
                            v-model="product.montaj.description"
                            hint=" "/>
            </div>
        </v-card-text>
    </v-card>
</template>

<script>
import { mapFields } from 'vuex-map-fields'
import DeleteEntry from './DeleteEntry'
export default {
  name: 'MontajOptional',
  components: { DeleteEntry },
  data () {
    return {
      dialog: false,
      currentIndex: null,
      sectionName: 'montaj',
      itemDefault: {
        isActive: false,
        name: '',
        price: '',
        thumb: ''
      }
    }
  },
  methods: {
    addItem () {
      this.product.montaj.items.push(Object.assign({}, this.itemDefault))
    },
    deleteItem () {
      this.product.montaj.items.splice(this.currentIndex, 1)
      this.dialog = false
    },
    openDialog (index) {
      this.currentIndex = index
      this.dialog = true
    }
  },
  computed: {
    ...mapFields([
      'product'
    ])
  }
}
</script>

