<script>
import { mapFields } from 'vuex-map-fields'
import DeleteEntry from './DeleteEntry'
export default {
  name: 'color',
  components: { DeleteEntry },
  data () {
    return {
      dialog: false,
      currentIndex: null,
      sectionName: 'color',
      itemDefault: {
        isActive: false,
        name: '',
        price: '',
        icon: '',
        photo: '',
        group: ''
      }
    }
  },
  methods: {
    addItem () {
      this.product.color.items.push(Object.assign({}, this.itemDefault))
    },
    deleteItem () {
      this.product.color.items.splice(this.currentIndex, 1)
      this.dialog = false
    },
    openDialog (index) {
      this.currentIndex = index
      this.dialog = true
    }
  },
  computed: {
    ...mapFields([
      'product'
    ])
  }
}
